import { createSlice } from '@reduxjs/toolkit'
import { getstrategies, deletestrategy, addstrategy } from "./strategiesApi";

const initialState = {
    strategies: [{ name: "All Strategies" }],
}
  
export const strategiesSlice = createSlice({
    name: 'strategies',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getstrategies.fulfilled, (state, action) => {
            state.strategies = action.payload;
            state.strategies.unshift({ name: "All Strategies" });            
        });
        builder.addCase(deletestrategy.fulfilled, (state, action) => {
            // console.log("Success Action: ", action.payload);
            // state.strategies = action.payload
        });
        builder.addCase(addstrategy.fulfilled, (state, action) => {
            // console.log("Success Action: ", action.payload);
            // state.strategies = action.payload
        });
    }
});

export default strategiesSlice.reducer