import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import MainPage from './app/mainpage';
import WatchList from './app/watchlist';
import Strategies from './app/strategies';
import reportWebVitals from './reportWebVitals';
import Socket from "./app/socket";
import {
  BrowserRouter,
  Routes,
  Route,
  HashRouter
} from "react-router-dom";
import { store } from './app/store';
import { Provider } from 'react-redux';
import Header from "./app/header";
import Backtesting from "./app/backtesting";
import { PersistGate } from 'redux-persist/integration/react';
import { persistStore } from 'redux-persist';

let persistor = persistStore(store);

ReactDOM.render(
<Provider store={store}>
  <PersistGate loading={null} persistor={persistor}>
    <BrowserRouter>
      <Header />
      <Routes>
        <Route path="/bp" element={<MainPage />} />
        <Route path="/bp/dashboard">
          <Route path="/bp/dashboard/watchlist" element={<WatchList />} />
          <Route path="/bp/dashboard/strategies" element={<Strategies />} />
          <Route path="/bp/dashboard/backtest" element={<Backtesting />} />
          <Route path="/bp/dashboard/socket" element={<Socket />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </PersistGate>
</Provider>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
