import axios from "axios";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { setsearchtermR } from './watchlistslice';
import { getwatchliststocks, getStocksDetailsApi, addstocktowatchlist, removestockfromwatchlist, searchstockApi, getStockChartData } from "./watchlistApi";
import RiskAmoutQuantity from "./RiskAmoutQuantity";

function WatchList() {
  const dispatch = useDispatch();

  const stockpricesR = useSelector((state) => state.watchlist.stockprices);  
  const searchlistR = useSelector((state) => state.watchlist.searchlist);
  const searchTermR = useSelector((state) => state.watchlist.searchterm);
  const watchlistR = useSelector((state) => state.watchlist.watchlist);
  const chartData = useSelector((state) => state.watchlist.chartData);

  // useEffect(() => {
  //   const delayDebounceFn = setTimeout(() => {
  //     console.log(searchTermR)
  //     // Send Axios request here
  //     // searchStock();
  //   }, 3000)

  //   return () => clearTimeout(delayDebounceFn)
  // },[searchTermR]);

  const handleChange = (event) => {
    dispatch(setsearchtermR(event.target.value));
    if (searchTermR.length == 0) {
        return;
    }
    // searchStock();
  };

  const handleKeypress = (e) => {
    //it triggers by pressing the enter key
    if (e.keyCode === 13) {
        if (searchTermR.length == 0) {
            alert("Please enter name of stock");
            return;
          }
      searchStock();
    }
  };

  const searchStock = () => {
    let objP = {};
    objP.searchTermR = searchTermR+"";
    objP.callback = () => {};
    dispatch(searchstockApi(objP));
  };

  const removeStock = (stockSymbol) => {
      let objP = {};
      objP.stockSymbol = stockSymbol;
      objP.callback = () => { 
        dispatch(getwatchliststocks((stocksData)=>{
          dispatch(getStocksDetailsApi(stocksData));
        }));
      }

    dispatch(removestockfromwatchlist(objP));
  }

  useEffect(() => {
    dispatch(getwatchliststocks((stocksData)=>{
      dispatch(getStocksDetailsApi(stocksData));
    }));
  }, []);

  return (
    <div className="App">
      <input
        type="text"
        placeholder="Search"
        value={searchTermR}
        onChange={handleChange}
        onKeyDown={handleKeypress}
      />
      <button
        onClick={() => {
          searchStock();
        }}
      >
        {" "}
        Search{" "}
      </button>
      <table border="1px solid black;">
        <th>Search List</th>
        <tr>
          <th>{"Exchange"}</th>
          <th>{"Name"}</th>
        </tr>
        {searchlistR.map((item) => {
          return (
            <tr>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {
                  <button
                    onClick={() => {
                      
                      let objP = {};
                      objP.stock = item;
                      objP.callback = () => { 
                        dispatch(getwatchliststocks((stocksData)=>{
                          dispatch(getStocksDetailsApi(stocksData));
                        }));
                      };
                      
                      dispatch(addstocktowatchlist(
                        objP
                      ));
                    }}
                  >
                    Add
                  </button>
                }
              </td>
            </tr>
          );
        })}
      </table>

      <br/>
      <div style={{display:"flex", flexDirection:"row"}}>
      <table border="1px solid black;">
        <tr><th>WatchList</th></tr>
        {watchlistR.length > 0 ? (
          <tr>
            <th></th>
            <th>{"Exchange"}</th>
            <th>{"Name"}</th> 
            <th>{"Price"}</th>
            <th></th>
            <th>SL</th>
            <th>TARGET</th>
            <th>  ENTRY  </th>
            <th>Profit%</th>
            {/* <th>Risk.Amt  | Quantity </th> */}
            <th>Total Amt  | Buy Qty </th>
          </tr>
        ) : null}
        {watchlistR.map((item) => {
          let lowArray = [];
          let sl = 0;
          let target = 0;
          let entry = stockpricesR[item.symbol];
          let profitPer = 0;
          let diff = 0;

          console.log("ChartData: ", JSON.stringify(chartData));
          if(chartData && chartData[item.symbol]) {
            lowArray = chartData[item.symbol].data.chart.result[0].indicators.quote[0].low;

            let lowLastPrice = lowArray.at(-1);
            let lowPrevLastPrice = lowArray.at(-2);
            if(lowPrevLastPrice < lowLastPrice)
              sl = lowPrevLastPrice - 1;
            else 
              sl = lowLastPrice - 1;
              target = (entry - sl) * 2 + entry;
              profitPer = (((entry - sl)*2)/entry) * 100;
              diff = Math.round(entry - sl);
          }

          return (
            <tr>
              <td>{<button onClick={()=>removeStock(item.symbol)}>Remove</button>}</td>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {stockpricesR[item.symbol]}
                {"  "}
              </td>
              <td>{<button onClick={()=>{
                let objP = {};
                objP.symbol = item.symbol;
                objP.sevendayprevts = Math.round(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).getTime()/1000);
                objP.currentdatets = Math.round(new Date().getTime()/1000);
                console.log("Seven day prev time: ", Math.round(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).getTime()/1000));
                objP.callback = () => {};
                dispatch(getStockChartData(objP));
              }}>Calculate</button>}</td>
              <td>{Math.round(sl)}</td>
              <td>{Math.round(target)}</td>
              <td>{Math.round(entry)}</td>
              <td>{Math.round(profitPer)}</td>
              <td><RiskAmoutQuantity entry={entry} /></td>
            </tr>
          );
        })}
      </table>
      </div>
    </div>
  );
}

export default WatchList;
