import { createAsyncThunk } from '@reduxjs/toolkit'
import axios from "axios";

export const getstrategies = createAsyncThunk(
    'getstrategies',
    async (callback, thunkAPI) => {
      const response = await axios.get('https://us-central1-stockwatch-193209.cloudfunctions.net/strategy');  
      callback(response.data);
      return response.data;
    }
  )

export const deletestrategy = createAsyncThunk(
    'deletestrategy',
    async ({strategy, callback}, thunkAPI) => {
      const response = await axios.post('https://us-central1-stockwatch-193209.cloudfunctions.net/strategy/delete', strategy);  
      callback(response.data);
      return response.data;
    }
)

export const addstrategy = createAsyncThunk(
    'addstrategy',
    async ({strategy, callback}, thunkAPI) => {
      const response = await axios.post('https://us-central1-stockwatch-193209.cloudfunctions.net/strategy', strategy);  
      callback(response.data);
      return response.data;
    }
)
